.Hero01 {
  position: relative;
  overflow: hidden;

  padding: 24rem 5rem 10rem;
}

.Hero01 h1,
.Hero01 p {
  color: var(--clr-2);
}

.Hero01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(3, 33.33%);
  gap: 0;
  justify-content: center;
  align-items: end;
}

.Hero01-box > img {
  position: absolute;
  z-index: 0;
  bottom: -100px;
  left: calc(50% - 150px);

  width: 300px;
  transform: scaleX(-1);
}

.Hero01-box::before {
  content: "";
  position: absolute;
  z-index: 0;
  bottom: -100px;
  left: calc(50% - 200px);

  width: 400px;
  height: 400px;
  background-color: var(--clr-3);
  border-radius: 50%;
  filter: blur(100px);
}

.Hero01-box > div:nth-child(1) {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: center;
}

.Hero01-box > div:nth-child(3) {
  width: 100%;
  padding-left: 5rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: end;
  align-items: center;
  text-align: right;
}

.Hero01-box > div {
  position: relative;
  z-index: 1;
}

.Hero01-box > div > div.buttons {
  width: 100%;
  margin-top: 1rem;

  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: end;
  align-items: end;
}

.Hero01-box > div > div.ca {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;

  background-color: var(--clr-1);
  border: solid 1px var(--clr-shadow);
  border-radius: 0;
  overflow: hidden;
}
.Hero01-box > div > div.ca p {
  width: 100%;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  overflow: hidden;
}
.Hero01-box > div > div.ca button {
  width: 3rem;
  height: 2.5rem;
  padding: 0;

  background-color: var(--clr-2);
  border: none;
  border-radius: 0;
}
.Hero01-box > div > div.ca button img {
  height: 1rem;
}

.Hero01-box > div > div.ca button:hover img {
  transform: scale(90%);
}

/*-- responsive --*/
@media screen and (max-width: 1480px) {
  .Hero01 {
    padding: 15rem 2rem 10rem;
  }
  .Hero01-box > img {
    bottom: 0;
    left: calc(50% - 90px);

    width: 180px;
  }

  .Hero01-box > div:nth-child(3) {
    padding-left: 0;
  }
}

@media screen and (max-width: 1000px) {
  .Hero01 {
    padding: 6rem 2rem 5rem;
  }
  .Hero01-box {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    justify-content: center;
    align-items: center;
  }
  .Hero01-box::before {
    bottom: -100px;
    left: unset;
    right: -100px;

    width: 300px;
    height: 300px;
    background-color: var(--clr-3);
    border-radius: 50%;
    filter: blur(100px);
  }

  .Hero01-box > img {
    position: absolute;
    z-index: 0;
    bottom: 0;
    right: -80px;
    left: unset;

    width: unset;
    height: 540px;
  }

  .Hero01-box > div:nth-child(1) {
    width: 100%;

    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    justify-content: start;
    align-items: center;
  }

  .Hero01-box > div:nth-child(3) {
    width: 100%;
    padding-left: 0;

    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    justify-content: start;
    align-items: start;
    text-align: left;
  }

  .Hero01-box > div > div.buttons {
    width: 100%;
    justify-content: start;
    align-items: start;
  }
  .Hero01-box > div > div.buttons > a,
  .Hero01-box > div > div.buttons > a button {
    width: 100%;
  }
}
