.BgVid02 {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;

  background-color: var(--clr-4);
  pointer-events: none;
}
.BgVid02 > video {
  width: 100%;

  opacity: 0.1;
  mask: linear-gradient(0deg, transparent, var(--clr-5));
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
}

@media screen and (max-width: 1000px) {
  .BgVid02 {
    height: 100%;
  }
  .BgVid02 > video {
    width: auto;
    height: 100%;
  }
}
