.Manifesto01 {
  position: relative;
  display: none;
}

.Manifesto01 h2,
.Manifesto01 h3,
.Manifesto01 p {
  color: var(--clr-1);
}

.Manifesto01-box {
  position: relative;
  width: 100%;
  padding: 5rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  background-image: none;
  background-position: top;
  background-repeat: no-repeat;
  background-size: contain;

  border: dotted 1px var(--clr-1);
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
}

@media screen and (max-width: 1000px) {
  .Manifesto01-box {
    padding: 2rem;
  }
}
