.Faq01 {
  background-color: var(--clr-2);
}
.Faq01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.Faq01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.Faq01-box {
  width: 80%;

  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
  align-items: center;
}
.Faq01-box > div:nth-child(1) {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.Faq01-box > div:nth-child(2) {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}
.Faq01-box > div:nth-child(2) > div {
  position: relative;

  width: 100%;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: start;
  align-items: start;

  border: solid 1px var(--clr-shadow);
  border-radius: 0.5rem;
  backdrop-filter: blur(5px);

  transition: 300ms;
}
.Faq01-box > div:nth-child(2) > div:hover {
  cursor: pointer;
  color: var(--clr-3);
}
.Faq01-box > div:nth-child(2) > div::after {
  content: "+";

  position: absolute;
  z-index: 1;
  top: 1rem;
  right: 2rem;

  font-weight: 600;
  color: var(--clr-p);
}
.Faq01-box > div:nth-child(2) > div:hover::after {
  color: var(--clr-3);
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
  .Faq01-box {
    width: 800px;
  }
}

@media screen and (max-width: 1000px) {
  .Faq01-box {
    width: 100%;
  }

  .Faq01-box > div:nth-child(1) {
    justify-content: start;
    align-items: start;
    text-align: left;
  }

  .Faq01-box > div:nth-child(2) > div {
    padding: 1rem 3rem 1rem 1rem;

    border-radius: 1rem;
  }

  .Faq01-box > div:nth-child(2) > div::after {
    top: 1rem;
    right: 1rem;
  }
}
