.About01 {
  padding-top: 15rem;
  background-color: var(--clr-2);
}

.About01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.About01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.About01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(2, calc(50% - 5rem));
  gap: 10rem;
  justify-content: center;
  align-items: center;
}

.About01-box > div {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;
}
.About01-box > div > a {
  margin-top: 1rem;
}

.About01-box > img {
  width: 100%;
  aspect-ratio: 1/1;
  justify-self: center;

  background-color: var(--clr-3);
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
  .About01 {
    padding-top: 10rem;
  }
  .About01-box > img {
    width: 100%;
  }
}

@media screen and (max-width: 1000px) {
  .About01 {
    padding-top: 3rem;
  }
  .About01-box {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: start;
    align-items: start;
  }
  .About01-box > img {
    width: 100%;
  }
}
