.Stats01 {
  position: relative;
  padding: 0 5rem;
}

.Stats01 .container {
  position: relative;
}

.Stats01-box {
  position: absolute;
  z-index: 1;

  width: 100%;

  display: flex;
  flex-direction: row;
  gap: 0;
  justify-content: space-between;
  align-items: stretch;

  background-color: var(--clr-bg);
}

.Stats01-box > div {
  width: 100%;
  padding: 3rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;

  border: solid 5px var(--clr-bg);
}

.Stats01-box > div:nth-child(1) {
  border: none;
  border-right: solid 5px var(--clr-2);
}
.Stats01-box > div:nth-child(3) {
  color: var(--clr-2);
  background-color: var(--clr-3);
  border-color: var(--clr-3);
}
.Stats01-box > div:nth-child(3) p {
  color: var(--clr-2);
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
}

@media screen and (max-width: 1000px) {
  .Stats01 {
    padding: 0;
  }

  .Stats01-box {
    position: relative;
    z-index: 1;

    width: 100%;

    display: flex;
    flex-direction: column;
    gap: 0;
    justify-content: start;
    align-items: start;
  }

  .Stats01-box > div {
    padding: 2rem;

    border: solid 3px var(--clr-bg);
  }

  .Stats01-box > div:nth-child(1) {
    border-right: none;
    border-bottom: solid 3px var(--clr-2);
  }
}
