:root {
  --clr-1: #072149;
  --clr-2: #ffffff;

  --clr-3: #4da2ff;
  --clr-4: #e24242;

  --clr-p: #092a5c;
  --clr-shadow: #002e74;

  --clr-bg: #ebebeb;
}

@font-face {
  font-family: DmSerif;
  src: url(./font/DMSerifDisplay-Regular.ttf);
}
@font-face {
  font-family: Roman;
  src: url(./font/TimesNewRoman.ttf);
}
@font-face {
  font-family: Inter;
  src: url(./font/Inter-Variable.ttf);
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "DmSerif", sans-serif;
}

p,
a,
li,
button {
  font-family: "Inter", sans-serif;
}

h1 {
  font-size: 4.5rem;
  font-weight: 500;
  line-height: 1;
  letter-spacing: -2px;
}
h2 {
  font-size: 3rem;
  font-weight: 600;
  line-height: 1.2;
}
h3 {
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.2;
}
h4 {
  font-size: 1.1rem;
  font-weight: 400;
}
h5 {
  font-size: 0.8rem;
  font-weight: 400;

  position: relative;
  padding: 0 0 0 10px;
  color: var(--clr-3);
}
h5::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 5px;
  left: 0px;

  width: 5px;
  height: 5px;
  background-color: var(--clr-3);
  border-radius: 1rem;
  animation: h5blink 1s infinite linear;
}

p,
li,
a,
button {
  font-size: 1rem;
  font-weight: 300;
  color: var(--clr-p);
}

a {
  color: var(--clr-1);
  transition: 0.3s;
}
a:hover {
  color: var(--clr-3);
}

span {
  font-style: italic;
  color: var(--clr-3);
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
:root {
  overflow-x: hidden;
}
body {
  position: relative;

  font-size: 1rem;
  font-weight: 400;
  font-family: "Bowlby", sans-serif;
  line-height: normal;
  letter-spacing: normal;
  color: var(--clr-1);
  background-color: var(--clr-2);

  overflow-x: hidden;
}

.section {
  width: 100%;
  padding: 10rem 5rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container {
  width: 1200px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.description {
  width: 75%;
  margin-bottom: 3rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

button {
  padding: 1rem 2rem;

  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  border-radius: 0;
  transition: 300ms;
}
button:hover {
  filter: brightness(50%);
  cursor: pointer;
}
button > img {
  height: 1rem;
}
.btn01 {
  color: var(--clr-1);
  background-color: transparent;
  border: solid 1px var(--clr-1);
}
.btn02 {
  color: var(--clr-2);
  background-color: var(--clr-bg);
  border: solid 1px var(--clr-2);
}
.btn03 {
  color: var(--clr-1);
  background-color: var(--clr-3);
  border: solid 1px var(--clr-3);
}
.btn04 {
  color: var(--clr-2);
  background-color: var(--clr-4);
  border: solid 1px var(--clr-4);
}
.btn04 img {
  filter: invert(1);
}

::-webkit-scrollbar {
  width: 0.5rem;

  background: var(--clr-1);
}
::-webkit-scrollbar-track {
  border: none;
}
::-webkit-scrollbar-thumb {
  border: solid 2px var(--clr-1);
  border-radius: 1rem;

  background: var(--clr-2);

  cursor: grab;
}

/*-- responsive --*/
@media screen and (max-width: 1480px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1rem;
  }

  .section {
    padding: 5rem 2rem;
  }
  .container {
    width: 900px;
  }
}
@media screen and (max-width: 1000px) {
  h1 {
    font-size: 15vw;
  }
  h2 {
    font-size: 8vw;
  }
  h3 {
    font-size: 6vw;
  }

  .section {
    padding: 3rem 1rem;
  }
  .container {
    width: 100%;
  }
  .description {
    width: 100%;
    margin-bottom: 2rem;

    gap: 0.5rem;
    justify-content: start;
    align-items: start;
    text-align: left;
  }

  button {
    padding: 0.5rem 1rem;
  }

  .btnFlex {
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
  }
  .btnFlex a,
  .btnFlex button {
    min-width: 220px;
  }
}
