.Doc01 {
  background-color: var(--clr-bg);
}
.Doc01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.Doc01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.Doc01-box {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(3, calc(33.33% - 5px));
  gap: 5px;
  justify-content: start;
  align-items: stretch;
}

.Doc01-box > div {
  width: 100%;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;

  background-color: var(--clr-2);
}
.Doc01-box > div:nth-child(2) {
  color: var(--clr-2);
  background-color: var(--clr-3);
}

.Doc01-box > div h4 {
  position: relative;
  z-index: 1;

  margin-bottom: 1rem;
  margin-left: 0.5rem;
  color: var(--clr-2);
}
.Doc01-box > div h4::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -0.25rem;
  left: -0.75rem;

  width: 2rem;
  height: 2rem;
  background-color: var(--clr-1);
  border-radius: 50%;
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
  .Doc01-box {
    grid-template-columns: repeat(3, calc(33.33% - 0.66rem));
    gap: 1rem;
  }
}

@media screen and (max-width: 1000px) {
  .Doc01-box {
    grid-template-columns: 100%;
  }
}
