.Features01 {
  background-color: var(--clr-2);
}
.Features01 > .container {
  opacity: 0;

  transform: translateY(50px); /* Start slightly below its final position */
  transition: opacity 1s ease-out, transform 1s ease-out;
}
.Features01 > .container.is-visible {
  opacity: 1;
  transform: translateY(0); /* Move to final position */
}

.Features01-box {
  width: 100%;

  display: grid;
  grid-template-columns: 40% 20% 20% 20%;
  gap: 0;
  justify-content: start;
  align-items: stretch;

  border-top: solid 1px var(--clr-shadow);
  border-left: solid 1px var(--clr-shadow);
}

.Features01-box > div {
  width: 100%;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: start;
  align-items: start;

  background-color: var(--clr-2);
  border-bottom: solid 1px var(--clr-shadow);
  border-right: solid 1px var(--clr-shadow);
}
.Features01-box > div > img {
  width: 1.5rem;
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
  .Features01-box {
    width: 100%;
  }
}

@media screen and (max-width: 1000px) {
  .Features01-box {
    display: grid;
    grid-template-columns: repeat(3, 33.33%);
  }
  .Features01-box > div {
    padding: 1rem;
  }
  .Features01-box > div:nth-child(1) {
    display: none;
  }
  .Features01-box > div.feature {
    grid-column-start: 1;
    grid-column-end: 4;
  }
}
