.Navbar01 {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;

  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.Navbar01 .container {
  padding: 1rem;
}

.Navbar01-box {
  width: 100%;
  padding: 5px;

  display: flex;
  flex-direction: row;
  gap: 5rem;
  justify-content: space-between;
  align-items: center;
}

.Navbar01-logo {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
}
.Navbar01-logo img {
  width: 1.5rem;
  height: 1.5rem;
}
.Navbar01-logo p {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--clr-2);
}
.Navbar01-logo p span {
  font-weight: 800;
}

.Navbar01-links {
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  gap: 5px;
}
.Navbar01-links li {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.Navbar01-links li a {
  padding: 5px 10px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;

  color: var(--clr-2);
  background-color: transparent;
  border: solid 1px var(--clr-2);
  border-radius: 5px;

  backdrop-filter: blur(5px);
}
.Navbar01-links li a:hover {
  color: var(--clr-2);
  background-color: var(--clr-3);
}
.Navbar01-links li a.btn01 {
  color: var(--clr-2);
  background-color: var(--clr-3);
}
.Navbar01-links li a.btn01:hover {
  color: var(--clr-2);
  background-color: var(--clr-1);
}
.Navbar01-links li a.btn01 img {
  height: 0.8rem;
  filter: invert(100%);
}
.Navbar01-links li a.btn01:hover img {
  filter: invert(0%);
}

.menu-button {
  position: relative;
  display: none;

  width: 1.5rem;
  height: 1.5rem;
  background-color: transparent;
  border: none;
  border-radius: 0;

  background-image: url(../img/icons/menu.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

/*-- responsive --*/

@media screen and (max-width: 1480px) {
  .Navbar01-box {
    gap: 5rem;
  }
}
@media screen and (max-width: 1000px) {
  .Navbar01-box {
    gap: 2rem;
  }
  .Navbar01-links > li {
    display: none;
  }
  .menu-button {
    display: block;
  }
}
